import React from "react";
import { graphql } from "gatsby";
import ExampleDetails from "../components/ExampleDetails";
import MetaTags from "../components/MetaTags";
import Header from "../components/Header";


function ExamplePage(props) {
  const {data, pageContext, location} = props;
  const {exampleImage} = data;
  const chart = {
    slug: pageContext.chartSlug,
    title: data.chart.frontmatter.title
  };
  let examples = data.chart.frontmatter.examples;
  examples = examples.filter(ex => ex.image);

  const getName = fname => fname.split(".")[0];
  const example = examples.find(example => getName(example.image) === pageContext.slug);
  const relatedExamples = examples.map( ({image}) => image && `${pageContext.chartSlug}/example/${getName(image)}`);

  return (
    <div>
      <MetaTags
        title={`Example of ${chart.title}: ${example.title}`}
        description={example.note}
        pathname={location.pathname}
        thumbnail={exampleImage && exampleImage.childImageSharp.original.src}
        url={data.site.siteMetadata.siteUrl}
      />
      <Header />
      <ExampleDetails example={example} exampleImage={exampleImage}  chart={chart} relatedExamples={relatedExamples} location={location} />
    </div>
  )
}

export default ExamplePage


export const query = graphql`
query ExampleQuery($filePath: String!, $chartSlug: String!) {
  chart:markdownRemark(fields: { slug: { eq: $chartSlug } }) {
    frontmatter {
      title
      examples {
        title
        author
        link
        note
        image
      }
    }
  }
  exampleImage:file(relativePath : { eq: $filePath }) {
      childImageSharp {
        original {
          width,
          height
          src
        }
      }
  }
  site {
    siteMetadata {
      siteUrl
    }
  }
}
`
