import React, { Component } from "react";
import InfoIcon from '../Header/icon-info.svg'
import styles from './styles.module.css'
import { URL } from 'whatwg-url'
import { Link } from "gatsby";

class ExampleDetails extends Component {

  constructor(props) {
    super(props);
    this.state = {
      isCollapsed: false
    };
  }

  toggleCollapse = (e) => {
    e.stopPropagation();
    this.setState({isCollapsed: !this.state.isCollapsed});
  }

  collapse = (e) => {
    e.stopPropagation();
    this.setState({isCollapsed: true});
  }

  // handleScroll = (e) => {
  //   this.setState({isCollapsed: true});
  // }
  //
  // componentDidMount() {
  //   window.addEventListener("scroll", this.handleScroll);
  // }
  //
  // componentWillUnmount() {
  //   window.removeEventListener("scroll", this.handleScroll);
  // }

  findCurrentIndex() {
    const exampleId = this.props.location.pathname.replace("/","");
    return this.props.relatedExamples.indexOf(exampleId);
  }

  next() {
    const posts = this.props.relatedExamples;
    const currentIndex = this.findCurrentIndex()
    if (currentIndex || currentIndex === 0) {
      // Wrap around if at end.
      return currentIndex + 1 === posts.length ? posts[0] : posts[currentIndex + 1];
    }
  }

  previous() {
    const posts = this.props.relatedExamples;
    const currentIndex = this.findCurrentIndex()
    if (currentIndex || currentIndex === 0) {
      // Wrap around if at start.
      return currentIndex === 0 ? posts.slice(-1)[0] : posts[currentIndex - 1]
    }
  }

  render() {
    const { example, exampleImage, chart} = this.props;
    const domain = example.link && getHostName(example.link);
    return (
      <>
        <div className={styles.backLink} >
          <Link to={`/${chart.slug}#examples`} title={`Back to ${chart.title}`}> Back to {chart.title} examples</Link>
        </div>
        <div className={styles.controls}>
          <Link className={styles.prev} to={"/" + this.previous()} title="previous">  </Link>
          <Link className={styles.next} to={"/" + this.next()} title="next">  </Link>
        </div>
        <div className={styles.exampleDetails} onClick={this.collapse}>
          <div className={`${styles.description} ${this.state.isCollapsed && styles.collapsed}`}>
              <InfoIcon className={styles.infoIcon} onClick={this.toggleCollapse}/>
              <div className={styles.collapsible}>
                <h2 className="title">{example.title}</h2>
                {example.author &&
                  <span className="author">by <em>{example.author}</em></span>
                }
                <p className="exampleDescription">{example.note}</p>
                {example.link &&
                  <div className={styles.link}>
                    Source: <a href={example.link} target="_blank">{domain}</a>
                  </div>
                }
              </div>
          </div>
          {exampleImage &&
          <img src={exampleImage.childImageSharp.original.src} alt=""/>
          }
        </div>
      </>
    )
  }
}

export default ExampleDetails

const getHostName = url => (new URL(url)).hostname;
